import React from "react";
import "../styles/LayoutStyles.css";
import logo_doctor from "../styles/doctorlogo.png";
import logo_user from "../styles/patientlogo.png";
import logo_admin from "../styles/adminnl.png";
import { SidebarMenu, adminMenu, userMenu } from "../Data/data";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";

const Layouts = ({ children }) => {
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.user);
  //console.log("User info", user);
  const location = useLocation();
  //console.log("User type", user?.isAdmin);

  //************** Doctor Menu ************
  const DoctorMenu = [
    {
      name: "Home",
      path: "/",
      icon: "fa-solid fa-house",
    },
    {
      name: "Appointment",
      path: "/doctor-appointments",
      icon: "fa-solid fa-list",
    },

    {
      name: "profile",
      path: `/doctor/profile/${user?._id}`,
      icon: "fa-solid fa-user",
    },
  ];
  //************** Doctor Menu ************

  const SidebarMenu = user?.isAdmin
    ? adminMenu
    : user?.isDoctor
    ? DoctorMenu
    : userMenu;

  const handelLogout = () => {
    localStorage.clear();
    toast.success("User logout successfully");
    navigate("/login");
  };

  return (
    <>
      <div className="main">
        <div className="layout">
          <div className="sidebar">
            <div>
              {user?.isAdmin ? (
                <img src={logo_admin} width={100} height={100} />
              ) : user?.isDoctor ? (
                <img src={logo_doctor} width={100} height={100} />
              ) : (
                <img src={logo_user} width={100} height={100} />
              )}
            </div>
            <div>
              {SidebarMenu.map((menu, key) => {
                const isActive = location.pathname === menu.path;
                return (
                  <>
                    <div
                      key={key}
                      className={`menu-iteam ${isActive && "active"}`}
                    >
                      <i className={menu.icon}></i>{" "}
                      <Link to={menu.path}>{menu.name}</Link>
                    </div>
                  </>
                );
              })}
              <div className="menu-iteam" onClick={handelLogout}>
                <i className="fa-solid fa-right-from-bracket"></i>
                <Link to="">Logout</Link>
              </div>
            </div>
          </div>

          <div className="content">
            <div className="header">
              <div className="header-content">
                <a href="" class="text-dark">
                  <i className="fa-sharp fa-solid fa-bell"></i>
                  <span
                    className="badge rounded-pill badge-notification bg-danger"
                    onClick={() => {
                      navigate("/notification");
                    }}
                  >
                    {user && user.notification.length}
                  </span>
                </a>
                <Link to="/profile">{user ? user.name : ""}</Link>
              </div>
            </div>
            <div className="body">{children}</div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Layouts;
