import React, { useEffect, useState } from "react";
import Layouts from "../../components/Layouts";
import axios from "axios";
import Moment from "react-moment";

const Appoinments = () => {
  const [appoinments, setAppoinments] = useState([]);

  const getAppointments = async () => {
    try {
      const res = await axios.get(
        "/api/v1/user/user-appointments",

        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );

      if (res.data.success) {
        setAppoinments(res.data.data);
      }
    } catch (error) {
      //console.log(error);
    }
  };

  useEffect(() => {
    getAppointments();
  }, []);

  const getStatusColor = (status) => {
    switch (status) {
      case "pending":
        return "orange";
      case "approved":
        return "green";
      case "Rejected":
        return "red";
      default:
        return "black"; // Default color for unknown status
    }
  };

  return (
    <Layouts>
      <h4>My Appointments</h4>
      <table class="table table-striped">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Dr. Name</th>
            <th scope="col">Dr.Phone</th>
            <th scope="col">Date</th>
            <th scope="col">Status</th>
          </tr>
        </thead>
        <tbody>
          {appoinments &&
            appoinments.map((record, i) => (
              <tr key={record._id}>
                <th scope="row">{i + 1}</th>
                <td>{record.doctorName}</td>
                <td>{record.doctorPhone}</td>
                <td>
                  <Moment format="Do MMM, YYYY, h:mm: a">{record.date}</Moment>
                </td>

                <td>
                  <span style={{ color: getStatusColor(record.status) }}>
                    {record.status.toUpperCase()}
                  </span>
                </td>
              </tr>
            ))}
        </tbody>
      </table>
    </Layouts>
  );
};

export default Appoinments;
