import React from "react";
import { useEffect, useState } from "react";
import Layouts from "../../components/Layouts";
import axios from "axios";
import rightsymbol from "../../styles/right-sign.jpg";
import wrongsymbol from "../../styles/wrongicon.png";

const Users = () => {
  const [users, setUsers] = useState([]);

  //getUsers
  const getUsers = async () => {
    try {
      const res = await axios.get("/api/v1/admin/getAllUsers", {
        headers: {
          authorization: "Bearer " + localStorage.getItem("token"),
          "content-type": "application/json",
        },
      });
      if (res.data.success) {
        //console.log(res.data.data);
        setUsers(res.data.data);
      }
    } catch (error) {
     // console.log(error);
    }
  };

  useEffect(() => {
    getUsers();
  }, []);

  return (
    <Layouts>
      <h4>All Users</h4>
      <table class="table table-striped">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Name</th>
            <th scope="col">Email</th>
            <th scope="col">Doctor?</th>
          </tr>
        </thead>
        <tbody>
          {users &&
            users.map((record, i) => (
              <tr key={record._id}>
                <th scope="row">{i + 1}</th>
                <td>
                  {record.name.charAt(0).toUpperCase() + record.name.slice(1)}
                </td>
                <td>{record.email}</td>
                <td>
                  {record.isDoctor ? (
                    <img src={rightsymbol} width={25} height={25} />
                  ) : (
                    <img src={wrongsymbol} width={25} height={25} />
                  )}
                </td>
              </tr>
            ))}
        </tbody>
      </table>
    </Layouts>
  );
};

export default Users;
