import React, { useEffect, useState } from "react";
import Layouts from "../../components/Layouts";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import { useDispatch } from "react-redux";
import { hideloading, showLoading } from "../../redux/features/alertSlice";
import Moment from "react-moment";

const DoctorAppointments = () => {
  const [appoinments, setAppoinments] = useState([]);
  const dispatch = useDispatch();

  const getAppointments = async () => {
    try {
      const res = await axios.get(
        "/api/v1/doctor/doctor-appointments",

        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );

      if (res.data.success) {
        setAppoinments(res.data.data);
      }
    } catch (error) {
      //console.log(error);
    }
  };

  const handelState = async (record, status) => {
    try {
      dispatch(showLoading());
      const res = await axios.post(
        "/api/v1/doctor/update-status",
        { appointmentsId: record._id, status },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        dispatch(hideloading());
        getAppointments();
        toast.success(res.data.message);
      }
    } catch (error) {
      dispatch(hideloading());
    }
  };

  useEffect(() => {
    getAppointments();
  }, []);

  return (
    <Layouts>
      <h1>Appointments Requets</h1>
      <table class="table table-striped">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Name</th>
            <th scope="col">Phone</th>
            <th scope="col">Date</th>
            <th scope="col">Status</th>
            <th scope="col">Action</th>
          </tr>
        </thead>
        <tbody>
          {appoinments &&
            appoinments.map((record, i) => (
              <tr key={record._id}>
                <th scope="row">{i + 1}</th>
                <td>{record.userInfo ? record.userInfo : ""}</td>
                <td>{record.userPhone}</td>
                <td>
                  <Moment format="Do MMM, YYYY, h:mm: a">{record.date}</Moment>
                </td>

                <td>{record.status.toUpperCase()}</td>

                <td>
                  {record.status == "pending" ? (
                    <div>
                      <button
                        className="btn btn-success"
                        onClick={() => handelState(record, "approved")}
                      >
                        Approve
                      </button>
                      <button
                        className="btn btn-danger"
                        onClick={() => handelState(record, "reject")}
                      >
                        Reject
                      </button>
                    </div>
                  ) : (
                    <span></span>
                  )}
                </td>
              </tr>
            ))}
        </tbody>
      </table>
      <ToastContainer />
    </Layouts>
  );
};

export default DoctorAppointments;
