import { React, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useDispatch } from "react-redux";
import { showLoading, hideloading } from "../redux/features/alertSlice";

const Register = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");

  const dispatch = useDispatch();

  const handleSubmit = async (e) => {
    e.preventDefault();

    //alert(name);
    const registerData = {
      name: name,
      email: email,
      password: password,
      phone: phone,
    };

    try {
      dispatch(showLoading());
      const res = await axios.post(
        "api/v1/user/register",
        JSON.stringify(registerData),
        { headers: { "content-type": "application/json" } }
      );
      //  const res = await fetch("http://localhost:8080/api/v1/user/register", {
      //     method: "POST",
      //     body: JSON.stringify({
      //       name: name,
      //       email: email,
      //       password: password,
      //     }),
      //     headers: {
      //         'Content-Type': 'application/json',
      //       },
      //    });
      // console.log(res);

      if (res.data.success) {
        dispatch(hideloading());
        toast.success(res.data.message);
        //alert('Registration successfully done');
      } else {
        toast.info(res.data.message);
      }
    } catch (error) {
      dispatch(hideloading());
      //console.log(error)
      toast.error(error);
    }
  };

  return (
    <>
      <div className="form-body">
        <div className="row">
          <div className="form-holder">
            <div className="form-content">
              <div className="form-items">
                <h3>Register Here</h3>
                <form onSubmit={handleSubmit} className="requires-validation">
                  <div className="col-md-12">
                    <input
                      className="form-control"
                      type="text"
                      name="name"
                      placeholder="Name"
                      onChange={(e) => setName(e.target.value)}
                      required
                    />
                    <div className="valid-feedback">
                      Username field is valid!
                    </div>
                    <div className="invalid-feedback">
                      Username field cannot be blank!
                    </div>
                  </div>

                  <div className="col-md-12">
                    <input
                      className="form-control"
                      type="email"
                      name="email"
                      placeholder="E-mail Address"
                      onChange={(e) => setEmail(e.target.value)}
                      required
                    />
                    <div className="valid-feedback">Email field is valid!</div>
                    <div className="invalid-feedback">
                      Email field cannot be blank!
                    </div>
                  </div>

                  <div className="col-md-12">
                    <input
                      className="form-control"
                      type="text"
                      name="phone"
                      placeholder="Phone"
                      onChange={(e) => setPhone(e.target.value)}
                      required
                    />
                    <div className="valid-feedback">
                      Phone number field is valid!
                    </div>
                    <div className="invalid-feedback">
                      Phone number field cannot be blank!
                    </div>
                  </div>

                  <div className="col-md-12">
                    <input
                      className="form-control"
                      type="password"
                      name="password"
                      placeholder="Password"
                      onChange={(e) => setPassword(e.target.value)}
                      required
                    />
                    <div className="valid-feedback">
                      Password field is valid!
                    </div>
                    <div className="invalid-feedback">
                      Password field cannot be blank!
                    </div>
                  </div>
                  <br></br>
                  <Link className="linkname" to="/login">
                    Already Registered!
                  </Link>

                  <div className="form-button mt-3">
                    <button type="submit" className="btn btn-primary">
                      Register
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <ToastContainer />
    </>
  );
};

export default Register;
