import React, { useState } from "react";
import Layouts from "../components/Layouts";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useSelector, useDispatch } from "react-redux";
import { hideloading, showLoading } from "../redux/features/alertSlice";
import { useNavigate } from "react-router-dom";

const ApplyDoctor = () => {
  const { user } = useSelector((state) => state.user);

  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [website, setWebsite] = useState("");
  const [address, setAddress] = useState("");
  const [specialization, setSpecialization] = useState("");
  const [experience, setExperience] = useState("");
  const [feesPerConsultation, setFeesPerConsultation] = useState("");
  const [timings, setTimings] = useState("");
  const [successMsg, setSuccessMsg] = useState("");

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const subApplyDocForm = async (e) => {
    e.preventDefault();

    // console.log(firstName);
    const applyDocData = {
      firstName: firstName,
      lastName: lastName,
      phone: phone,
      email: email,
      website: website,
      address: address,
      specialization: specialization,
      experience: experience,
      feesPerConsultation: feesPerConsultation,
      timings: timings,
      userId: user._id,
    };

    try {
      dispatch(showLoading());
      const res = await axios.post(
        "api/v1/user/apply-doctor",
        JSON.stringify(applyDocData),
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("token"),
            "content-type": "application/json",
          },
        }
      );
      //console.log(res);
      if (res.data.success) {
        dispatch(hideloading());
        //alert(res.data.message);
        setSuccessMsg("successfully submitted");
        toast.success(res.data.message);
        // navigate("/");
      } else {
        dispatch(hideloading());
        toast.info(res.data.error.message);
      }
    } catch (error) {
      // console.log(error);
      dispatch(hideloading());
      toast.info("Something went wrong");
    }
  };

  return (
    <Layouts>
      {/* <h2>Apply Doctor</h2> */}

      <form onSubmit={subApplyDocForm}>
        <div className="form-row row custom-row">
          <div className="docheading">
            <center>
              <span style={{ color: "green" }}>{successMsg}</span>
            </center>
            <p>Personal Info</p>
          </div>
          <div className="col-md-6">
            <input
              type="text"
              name="firstName"
              className="form-control"
              placeholder="First name"
              onChange={(e) => setFirstName(e.target.value)}
            />
          </div>
          <div className="col-md-6 ">
            <input
              type="text"
              className="form-control"
              placeholder="Last name"
              name="lastName"
              onChange={(e) => setLastName(e.target.value)}
            />
          </div>
          <br></br>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Phone Number"
              name="phone"
              onChange={(e) => setPhone(e.target.value)}
            />
          </div>
          <div className="col-md-6  custom-six">
            <input
              type="email"
              className="form-control"
              placeholder="E-mail"
              name="emial"
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <br></br>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Website"
              name="website"
              onChange={(e) => setWebsite(e.target.value)}
            />
          </div>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Address"
              name="address"
              onChange={(e) => setAddress(e.target.value)}
            />
          </div>
          <br></br>
          <div className="docheading">
            <p>Professional Info</p>
          </div>
          <div className="col-md-6 custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="specialization"
              name="specialization"
              onChange={(e) => setSpecialization(e.target.value)}
            />
          </div>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Experience"
              name="experience"
              onChange={(e) => setExperience(e.target.value)}
            />
          </div>
          <br></br>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Consultation Fee"
              name="feesPerConsultation"
              onChange={(e) => setFeesPerConsultation(e.target.value)}
            />
          </div>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Timing"
              name="timings"
              onChange={(e) => setTimings(e.target.value)}
            />
          </div>
          <div className="col-md-3  custom-six"></div>
          <div className="col-md-6  custom-six">
            <button type="submit" className="btn btn-primary">
              Submit
            </button>
          </div>
          <div className="col-md-3  custom-six"></div>
        </div>
      </form>
      <ToastContainer />
    </Layouts>
  );
};
export default ApplyDoctor;
