import React, { useEffect, useState } from "react";
import Layouts from "../components/Layouts";
import axios from "axios";
import { useParams } from "react-router-dom";
import { hideloading, showLoading } from "../redux/features/alertSlice";
import { useDispatch, useSelector } from "react-redux";
import { ToastContainer, toast } from "react-toastify";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const BookingPage = () => {
  const { user } = useSelector((state) => state.user);
  const params = useParams();
  const [doctors, setDoctors] = useState([]);
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [isAvailable, setIsAvailable] = useState(false);
  const [startDate, setStartDate] = useState(new Date());

  const dispatch = useDispatch();

  const getUserData = async () => {
    try {
      const res = await axios.post(
        "/api/v1/doctor/getDoctorById",
        { doctorId: params.doctorId },
        {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        setDoctors(res.data.data);
       // console.log(doctors);
      }
    } catch (error) {
     // console.log(error);
    }
  };

  // ============ handle availiblity
  const handleAvailability = async () => {
    try {
      dispatch(showLoading());
      const res = await axios.post(
        "/api/v1/user/booking-availbility",
        { doctorId: params.doctorId, date, time },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
        dispatch(hideloading());
        setIsAvailable(true);
        //console.log(isAvailable);
        toast.success(res.data.message);
        // jQuery("#show_ajax_res").html(
        //   '<span style="color:green;">' + res.data.message + "<span>"
        // );
      } else {
        toast.info(res.data.message);
      }
    } catch (error) {
      dispatch(hideloading());
      //console.log(error);
    }
  };

  //booking func
  const handleBooking = async () => {
    try {
      dispatch(showLoading());

      const res = await axios.post(
        "/api/v1/user/book-appointment",
        {
          doctorId: params.doctorId,
          userId: user._id,
          doctorInfo: doctors,
          doctorName: doctors.firstName,
          doctorPhone: doctors.phone,
          userInfo: user.name,
          userPhone: user.phone,
          date: date,
          time: time,
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      if (res.data.success) {
      //  console.log(res);
        dispatch(hideloading());
        toast.success(res.data.message);
      }
    } catch (error) {
      dispatch(hideloading());
     // console.log(error);
    }
  };

  useEffect(() => {
    getUserData();
  }, []);

  return (
    <Layouts>
      <p id="show_ajax_res"></p>
      <h2>Booking Page </h2>
      <div className="container m-2">
        {doctors && (
          <div>
            <h4>
              Dr.{doctors.firstName} {doctors.lastName}
            </h4>
            <h4>Fees : {doctors.feesPerConsultation}</h4>
            <h4>Timings : {doctors.timings && doctors.timings}</h4>
            <br />
            <div className="col-md-6">
              <DatePicker
                showTimeSelect
                className="form-control"
                name="date"
                selected={date}
                dateFormat="d-MM-yyyy h:mmaa"
                placeholder="date"
                onChange={(date) => setDate(date)}
              />
            </div>
            <br />

            {/* <br />
            <div className="col-md-6 ">
              <input
                type="text"
                className="form-control"
                placeholder="Time"
                name="time"
                onChange={(e) => setTime(e.target.value)}
              />
            </div> */}

            <div className="d-flex flex-column w-50">
              {/* <button
                className="btn btn-primary mt-2"
                onClick={handleAvailability}
              >
                Check Availability
              </button> */}

              <button className="btn btn-dark mt-2" onClick={handleBooking}>
                Book Now
              </button>
            </div>
          </div>
        )}
      </div>
      <ToastContainer />
    </Layouts>
  );
};

export default BookingPage;
