import React from "react";
import Layouts from "../components/Layouts";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import { useSelector, useDispatch } from "react-redux";
import { hideloading, showLoading } from "../redux/features/alertSlice";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";

const NotificationPage = () => {
  const { user } = useSelector((state) => state.user);

  const dispatch = useDispatch();

  const handelMarkAllRead = async () => {
    try {
      dispatch(showLoading);
      const res = await axios.post(
        "api/v1/user/get-all-notification",
        { userId: user._id },
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("token"),
            "content-type": "application/json",
          },
        }
      );
      dispatch(hideloading);
      //console.log(res);
      if (res.data.success) {
        toast.success(res.data.message);
      }
    } catch (error) {
      dispatch(hideloading);
      toast.info("Something went wrong");
    }
  };

  //delete notificaiton
  const handelDeleteAllRead = async () => {
    try {
      dispatch(showLoading);
      const res = await axios.post(
        "api/v1/user/delete-all-notification",
        { userId: user._id },
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("token"),
            "content-type": "application/json",
          },
        }
      );
      dispatch(hideloading);
      //console.log(res);
      if (res.data.success) {
        toast.success(res.data.message);
      } else {
        toast.info(res.data.message);
      }
    } catch (error) {
      //console.log(error);
      dispatch(hideloading);
      toast.info("Something went wrong");
    }
  };

  return (
    <Layouts>
      <p>Notificaiton Page</p>

      <Tabs
        defaultActiveKey="Unread"
        id="uncontrolled-tab-example"
        className="mb-3"
      >
        <Tab eventKey="Unread" title="Unread">
          <button onClick={handelMarkAllRead}>Make all read</button>
          {user?.notification.map((notificationMsg) => (
            <p>{notificationMsg.message}</p>
          ))}
        </Tab>
        <Tab eventKey="Read" title="Read">
          <button onClick={handelDeleteAllRead}>Delete all read</button>
          {user?.seenNotification.map((notificationMsg) => (
            <p>{notificationMsg.message}</p>
          ))}
        </Tab>
      </Tabs>
      <ToastContainer />
    </Layouts>
  );
};

export default NotificationPage;
