const ProfilePage = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [website, setWebsite] = useState("");
  const [address, setAddress] = useState("");
  const [specialization, setSpecialization] = useState("");
  const [experience, setExperience] = useState("");
  const [feesPerConsultation, setFeesPerConsultation] = useState("");
  const [timings, setTimings] = useState("");

  const { user } = useSelector((state) => state.user);
  const [doctor, setDoctor] = useState(null);
  const params = useParams();

  //Get Doctor info
  const getDoctorInfo = async () => {
    try {
      const res = await axios.post(
        "/api/v1/doctor/getdoctorinfo",
        { userId: params.id },
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        setDoctor(res.data.data);
        //console.log(res.data.data);
        setFirstName(res.data.data.firstName);
        setLastName(res.data.data.lastName);
        setPhone(res.data.data.phone);
        setEmail(res.data.data.email);
        setWebsite(res.data.data.website);
        setAddress(res.data.data.address);
        setSpecialization(res.data.data.specialization);
        setExperience(res.data.data.experience);
        setFeesPerConsultation(res.data.data.feesPerConsultation);
        setTimings(res.data.data.timings);
      }
    } catch (error) {
     // console.log(error);
    }
  };

  useEffect(() => {
    getDoctorInfo();
  }, []);

  const updateDocProfileForm = async (e) => {
    e.preventDefault();
    // console.log(firstName);
    const updateDocData = {
      firstName: firstName,
      lastName: lastName,
      phone: phone,
      email: email,
      website: website,
      address: address,
      specialization: specialization,
      experience: experience,
      feesPerConsultation: feesPerConsultation,
      timings: timings,
      userId: user._id,
    };

    try {
      dispatch(showLoading());
      const res = await axios.post(
        "/api/v1/doctor/updateProfile",
        JSON.stringify(updateDocData),
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("token"),
            "content-type": "application/json",
          },
        }
      );
     // console.log(res);
      if (res.data.success) {
        dispatch(hideloading());
        toast.success(res.data.message);
        // navigate("/");
      } else {
        dispatch(hideloading());
        toast.info(res.data.error.message);
      }
    } catch (error) {
     // console.log(error);
      dispatch(hideloading());
      toast.info("Something went wrong");
    }
  };

  return (
    <Layouts>
      {/* <h2>Apply Doctor</h2> */}
      <form onSubmit={updateDocProfileForm}>
        <div className="form-row row custom-row">
          <div className="docheading">
            <p>Personal Info</p>
          </div>
          <div className="col-md-6">
            <input
              type="text"
              name="firstName"
              className="form-control"
              placeholder="First name"
              value={firstName}
              onChange={(e) => setFirstName(e.target.value)}
            />
          </div>
          <div className="col-md-6 ">
            <input
              type="text"
              className="form-control"
              placeholder="Last name"
              name="lastName"
              value={lastName}
              onChange={(e) => setLastName(e.target.value)}
            />
          </div>
          <br></br>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Phone Number"
              name="phone"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
            />
          </div>
          <div className="col-md-6  custom-six">
            <input
              type="email"
              className="form-control"
              placeholder="E-mail"
              name="emial"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <br></br>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Website"
              name="website"
              value={website}
              onChange={(e) => setWebsite(e.target.value)}
            />
          </div>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Address"
              name="address"
              value={address}
              onChange={(e) => setAddress(e.target.value)}
            />
          </div>
          <br></br>
          <div className="docheading">
            <p>Professional Info</p>
          </div>
          <div className="col-md-6 custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="specialization"
              name="specialization"
              value={specialization}
              onChange={(e) => setSpecialization(e.target.value)}
            />
          </div>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Experience"
              name="experience"
              value={experience}
              onChange={(e) => setExperience(e.target.value)}
            />
          </div>
          <br></br>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Consultation Fee"
              name="feesPerConsultation"
              value={feesPerConsultation}
              onChange={(e) => setFeesPerConsultation(e.target.value)}
            />
          </div>
          <div className="col-md-6  custom-six">
            <input
              type="text"
              className="form-control"
              placeholder="Timing"
              name="timings"
              value={timings}
              onChange={(e) => setTimings(e.target.value)}
            />
          </div>
          <div className="col-md-3  custom-six"></div>
          <div className="col-md-6  custom-six">
            <button type="submit" className="btn btn-primary">
              Update
            </button>
          </div>
          <div className="col-md-3  custom-six"></div>
        </div>
      </form>
      <ToastContainer />
    </Layouts>
  );
};

export default ProfilePage;
