import React, { useEffect, useState } from "react";
import Layouts from "../components/Layouts";
import { useDispatch } from "react-redux";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { hideloading, showLoading } from "../redux/features/alertSlice";

const UserProfile = () => {
  const [firstName, setFirstName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");

  const dispatch = useDispatch();

  const profileInfo = async () => {
    try {
      const res = await axios.get("/api/v1/user/getuserinfo", {
        headers: {
          authorization: "Bearer " + localStorage.getItem("token"),
          "content-type": "application/json",
        },
      });
      if (res.data.success) {
        //setUserInfo(res.data.data);
        //console.log("RESSSS" + JSON.stringify(res.data));
        setFirstName(res.data.data[0].name);
        //alert(firstName);
        //setLastName(res.data.data.lastName);
        setPhone(res.data.data[0].phone);
        setEmail(res.data.data[0].email);
      }
    } catch (error) {
      //console.log(error);
    }
  };

  useEffect(() => {
    profileInfo();
  }, []);

  const updateUserProfileForm = async (e) => {
    e.preventDefault();
    const updateUserData = {
      name: firstName,
      phone: phone,
      email: email,
    };
    try {
      dispatch(showLoading());
      const res = await axios.post(
        "/api/v1/user/updateUserProfile",
        JSON.stringify(updateUserData),
        {
          headers: {
            authorization: "Bearer " + localStorage.getItem("token"),
            "content-type": "application/json",
          },
        }
      );
      console.log(res);
      if (res.data.success) {
        dispatch(hideloading());
        toast.success(res.data.message);
        // navigate("/");
      } else {
        dispatch(hideloading());
        toast.info(res.data.error.message);
      }
    } catch (error) {
      console.log(error);
      dispatch(hideloading());
      toast.info("Something went wrong");
    }
  };
  return (
    <Layouts>
      {/* <h2>Apply Doctor</h2> */}
      <form onSubmit={updateUserProfileForm}>
        <div className="form-row row custom-row">
          <div className="docheading">
            <h4>Personal Info</h4>
          </div>

          <div className="col-md-6">
            <label>Name</label>
            <input
              type="text"
              name="firstName"
              className="form-control"
              placeholder="First name"
              value={firstName ? firstName : ""}
              onChange={(e) => setFirstName(e.target.value)}
            />
          </div>

          <div className="col-md-6">
            <label>E-mail</label>
            <input
              type="email"
              className="form-control"
              placeholder="E-mail"
              name="emial"
              value={email ? email : ""}
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>

          <div className="col-md-6 ">
            <label>Phone</label>
            <input
              type="text"
              className="form-control"
              placeholder="Phone Number"
              name="phone"
              value={phone ? phone : ""}
              onChange={(e) => setPhone(e.target.value)}
            />
          </div>

          <br></br>

          <div className="col-md-3  custom-six"></div>
          <div className="col-md-6  custom-six">
            <button type="submit" className="btn btn-primary">
              Update
            </button>
          </div>
          <div className="col-md-3  custom-six"></div>
        </div>
      </form>
      <ToastContainer />
    </Layouts>
  );
};

export default UserProfile;
